import React, { useEffect } from "react";
import { createRoot } from 'react-dom/client';
import { Provider } from 'react-redux';
import App from "./App";
import { store } from "./store/store";
import PersistProvider from "./store/providers/persist-provider";
import { setProducts } from "./store/slices/product-slice"
import { setRequests } from "./store/slices/request-slice"
import 'animate.css';
import 'swiper/swiper-bundle.min.css';
import "yet-another-react-lightbox/styles.css";
import "yet-another-react-lightbox/plugins/thumbnails.css";
import "./assets/scss/style.scss";
import "./i18n";
import axios from "axios";
// import products from "./data/products.json";

// store.dispatch(setProducts(products));
// console.log("",products)
// var apiUrl = "http://localhost:8001"; //
var apiUrl = ""; //

// allProducts DB  
//   json  batch 

axios.post(apiUrl+'/allProducts').then(function (response) {
  console.log(response.data);
  let products = response.data;
  store.dispatch(setProducts(products));
}).catch(function(error) {

}).then(function () {
  //finally
});


axios.post(apiUrl+'/allRequests').then(function (response) {
  console.log(response.data);
  let requests = response.data;
  store.dispatch(setRequests(requests));
  
}).catch(function(error) {

}).then(function () {
  //finally
});

const container = document.getElementById('root');
const root = createRoot(container);
root.render(
    <Provider store={store}>
      <PersistProvider>
        <App />
      </PersistProvider>
    </Provider>
);

